    .cardStyle {
        width: 20rem;
        height: 20rem;
        margin: 16px auto;
        box-shadow: 0px 2px 1px -1px #916aad, 0px 1px 1px 0px #714d92, 0px 1px 3px 0px #865fa2 !important;
    }
    .buttonContainer,
    .buttonContainer > button{
        width:100%;
    }
    .buttonContainer > button{
        text-align: center;
    }
    .cardStyle > img {
        background-size: cover;
        background-repeat: no-repeat;
        -webkit-background-position: center;
        background-position: center;
        width:auto;
        display:inline;
        object-fit: contain !important;
    }
